












































































































































































.questions-container {
  margin-bottom: 40px;

  img {
    left: 0;
    position: absolute;
  }

  nav {
    text-align: center;
  }
}
.questions-container .row {
  justify-content: flex-end;
  padding-bottom: 60px;
  padding-top: 100px;
}
.questions-container h2 {
  color: #636363;
  margin-bottom: 40px;
  font-size: 38px;
  text-transform: uppercase;
  font-weight: 400;
  margin-left: -15px;
  font-family: 'eucerinawgl-regular-webfont';
}
.btn {
  padding: 0;
  + .btn {
    margin-left: 25px;
  }
}

.back-btn {
  height: 64px;
  left: -64px;
  width: 64px;
}

.back-btn:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}
.progress-circle-bar {
  position: absolute;
  width: 35px;
  height: 35px;
  border: 2px solid #878c92;
  display: flex;
  align-content: center;
  align-items: center;
  align-self: center;
  justify-content: center;
  justify-items: center;
  justify-self: center;
  border-radius: 50%;
  place-items: center;
  top: 35px;
  right: -85px;
  z-index: 9999;

  &:before {
    content: '';
    position: absolute;
    width: calc(100% - 12px);
    border: 2px solid #878c92;
    height: calc(100% - 12px);
    background: white;
    border-radius: 50%;
  }
}
